import { withNavigationWatcher } from './contexts/navigation';
import { HomePage, TasksPage, ProfilePage, AboutPage, About2Page, MonitoringPage, DashboardPage, PendingPage, ReportingPage, SearchPage, AuthorizationPage } from './pages';

const routes = [
  {
    path: '/tasks',
    component: TasksPage
  },
  {
    path: '/profile',
    component: ProfilePage
  },
  {
    path: '/home',
    component: HomePage
  },
  {
    path: '/about',
    component: AboutPage
  }, 
  {
    path: '/monitoring',
    component: MonitoringPage
  }, 
  {
    path: '/dashboard',
    component: DashboardPage
  }, 
  {
    path: '/pending',
    component: PendingPage
  }, 
  {
    path: '/reporting',
    component: ReportingPage
  }, 
  {
    path: '/search',
    component: SearchPage
  }, 
  {
    path: '/authorization',
    component: AuthorizationPage
  }
];

export default routes.map(route => {
  return {
    ...route,
    component: withNavigationWatcher(route.component)
  };
});
