function filterBox(self) {
  return (
    <Box direction="row" width="27em" style={{margin: "0.5em"}}>
    <Item baseSize="10em">
      <div height="100%" style={{ marginTop: "0.4em", fontSize: "16px" }}>
        Cycle Date / วันที่ :
      </div>
    </Item>
    <Item baseSize="10em">
      <DateBox defaultValue={self.now} type="date" />
    </Item>
    <Item baseSize="7em">
      <Button text="Search"></Button>
    </Item>
  </Box>
  );
}

