const COL1 = '10em';
const COL2 = '15em';
const COL3 = '12em';

function rowA(self) {
  return (
    <React.Fragment>
      <Box direction="row" height="100%">
        <Item baseSize="3em">
          <div style={{
              backgroundColor : "red",
              display         : "flex",
              height          : "100%"
          }}>&nbsp;</div>
        </Item>
        <Item ratio={1} baseSize={ COL1 }>
          { boxA1(self) }
        </Item>
        <Item ratio={1} baseSize={ COL2 }>
          { boxA2(self) }
        </Item>
        <Item ratio={1} baseSize={ COL3 }>
          { boxA3(self) }
        </Item>
      </Box>
    </React.Fragment>
  );
}

