function randomPick(items) {
    return items[Math.floor(Math.random() * items.length)];
}

function genApp() {
    const APPS = ['AIA', 'XEROX'];
    return randomPick(APPS);
}

function genPolicy() {
    return 'T' + parseInt(Math.random() * 10000000);
}

function genJob() {
    return 'PD' + parseInt(Math.random() * 1000000);
}

function genDocGroup() {
    const GROUPS = ['e-Doc', 'e-Policy', 'e-Receipt', 'e-Endorsement'];
    return randomPick(GROUPS);
}

function genStatus() {
    const STATUSES = ['Succes', 'Failed', 'Error'];
    return randomPick(STATUSES);
}

function genSubject() {
    const NAMES = ['สมชาย', 'สมศักดิ์', 'สมปอง', 'สมศรี', 'สมฤทัย', 'สมประสงค์', 'สมฤดี', 'สมหญิง', 'สมเกียรติ'];
    const MONTHS = [ 'มกราคม', 'กุมภาพันธ์', 'มีนาคม', 'เมษายน', 'พฤษภาคม', 'มิถุนายน',
                     'กรกฎาคม', 'สิงหาคม', 'กันยายน', 'ตุลาคม', 'พฤศจิกายน', 'ธันวาคม' ];
    const YEARS = ['2559', '2560', '2561', '2562', '2563'];
    return 'รายการสินไหมของคุณ' + randomPick(NAMES) + ' (' + randomPick(MONTHS) + ' ' + randomPick(YEARS) + ')';
}

function genAttachmet() {
    return 'file_' + parseInt(Math.random() * 10000000) + '.pdf';
}

function genSendDate() {
    return new Date(Date.now() - Math.random() * 1000 * 60 * 60 * 6).toISOString();
}

function genRecipient() {
    const HOSTS = ['gmail.com', 'hotmail.com', 'outlook.com'];
    return 'cus' + parseInt(Math.random() * 1000) + '@' + randomPick(HOSTS);
}

function genReason() {
    return '...';
}

function genDetailData() {
    let r = [];
    for (let i = 0; i < 500; i++) {
        r.push({
            ID: i + 1,
            App: genApp(),
            Job: genJob(),
            Policy: genPolicy(),
            Doc: genDocGroup(),
            Status: genStatus(),
            Subject: genSubject(),
            Attachment: genAttachmet(),
            SendDate: genSendDate(),
            Recipient: genRecipient(),
            Reason: genReason(),
            CycleDate: '2021-02-21'
        });
    }
    return r;
}

const DETAIL_DATA = genDetailData();

function detailGrid(self) {
  return (
    <React.Fragment>
      <DataGrid
        dataSource={DETAIL_DATA}
        allowColumnReordering={true}
        allowColumnResizing={true}
        showBorders={true}
      >
        <Export enabled={true} />
        <GroupPanel visible={true} />
        <SearchPanel visible={true} highlightCaseSensitive={true} />
        <Grouping autoExpandAll={false} />
        <Column dataField="ID"         dataType="number" caption="#"                 minWidth={ 80} width={ 80} />
        <Column dataField="App"        dataType="string" caption="App Owner"         minWidth={ 90} width={ 90} />
        <Column dataField="Job"        dataType="string" caption="Job Code"          minWidth={ 95} width={ 95} />
        <Column dataField="Policy"     dataType="string" caption="Policy No"         minWidth={ 95} width={ 95} />
        <Column dataField="Doc"        dataType="string" caption="Doc Group"         minWidth={ 95} width={ 95} />
        <Column dataField="Status"     dataType="string" caption="Status"            minWidth={ 80} width={ 80} />
        <Column dataField="Subject"    dataType="string" caption="Subject"           minWidth={150} width={"auto"} />
        <Column dataField="Attachment" dataType="string" caption="Attachment Name"   minWidth={100} width={"auto"} />
        <Column dataField="SendDate"   dataType="string" caption="Send out Date"     minWidth={120} width={120} />
        <Column dataField="Recipient"  dataType="string" caption="Recipient Email"   minWidth={150} width={150} />
        <Column dataField="Reason"     dataType="string" caption="Reason"            minWidth={ 80} width={ 80} />
        <Column dataField="CycleDate"  dataType="string" caption="Cycle Date"        minWidth={120} width={120} />
        <Pager allowedPageSizes={1000} showPageSizeSelector={true} />
        <Paging defaultPageSize={10} />
      </DataGrid>
    </React.Fragment>
  );
}

class App extends React.Component {
  render() {
    const HEIGHT = "10.5em";
    return (
      <React.Fragment>
        { filterBox(this) }
            <Box direction="col" width="80em" style={{margin: "1em"}}>
          <Item baseSize={ HEIGHT }>{ rowA(this) }</Item>
          <Item baseSize={ HEIGHT }>{ rowB(this) }</Item>
          <Item baseSize={ HEIGHT }>{ rowC(this) }</Item>
        </Box>
        { detailGrid(this) }
      </React.Fragment>
    );
  }
}

export default App;
