import React from 'react';
import './dashboard.scss';

import Box, {
  Item
} from 'devextreme-react/box';

import DateBox from 'devextreme-react/date-box';
import { Button } from 'devextreme-react';

import Chart, {
  Series,
  Legend,
  CommonSeriesSettings,
  Point,
  ArgumentAxis,
  Export
} from 'devextreme-react/chart';

import RangeSelector, {
  Size,
  Chart as ChartOptions,
  Margin,
  Scale,
  Behavior,
  TickInterval, MinorTickInterval, SliderMarker, MinorTick
} from 'devextreme-react/range-selector';

const STATUSES = [
  { value: 'success', name: 'Success' },
  { value: 'failed',  name: 'Failed' },
  { value: 'error',   name: 'Error' }
];

function rand(mid, range) {
    let v = Math.random() * range;
    return parseInt((mid - (range / 2)) + v);
}

const ONE_DAY = 1000 * 60 * 60 * 24;

function getRange() {
    const DAYS = 183;
    let startValue = new Date(new Date() - (DAYS * ONE_DAY)).valueOf();
    let endValue = new Date(new Date() -  ONE_DAY).valueOf();
    return {
        start: new Date(startValue),
        end: new Date(endValue)
    };
}

const RANGE = getRange();
const INITIAL_RANGE = { start: new Date(RANGE.end - (30 * ONE_DAY)), end: RANGE.end };

function genDaily() {
    let { start, end } = RANGE;
    let r = [];
    let d = start.valueOf();
    while (d < end.valueOf()) {
        d  = d + ONE_DAY;
        let success = rand(100, 40);
        let error   = rand(14, 13);
        let failed  = rand(19, 17);
        r.push({ d: new Date(d), success, error, failed });
    }
    return r;
}

const DATA = genDaily();

function filterBox(self) {
  return (
    <Box direction="row" width="40em" style={{margin: "0.5em"}}>
      <Item baseSize="10em">
        <div height="100%" style={{ marginTop: "0.4em", fontSize: "16px" }}>
          Cycle Date / วันที่ :
        </div>
      </Item>
      <Item baseSize="10em">
        <DateBox type="date" />
      </Item>
      <Item baseSize="3em">
        <div height="100%" style={{ marginTop: "0.4em", fontSize: "16px" }}>
          ถึง :
        </div>
      </Item>
      <Item baseSize="10em">
        <DateBox type="date" />
      </Item>
      <Item baseSize="7em">
        <Button text="Search"></Button>
      </Item>
    </Box>
  );
}

function box(self, title) {
  return (
    <React.Fragment>
      { aChart(self, title) }
    </React.Fragment>
  );
}

const COL1 = '10em';

function rowA(self) {
  return (
    <React.Fragment>
      <Box direction="row" height="100%">
        <Item ratio={1} baseSize="50%">
          { box(self, 'e-Policy status') }
        </Item>
        <Item ratio={1} baseSize="50%">
          { box(self, 'e-Endorsement status') }
        </Item>
      </Box>
    </React.Fragment>
  );
}

function rowB(self) {
  return (
    <React.Fragment>
      <Box direction="row" height="100%">
        <Item ratio={1} baseSize="50%">
          { box(self, 'e-Doc status') }
        </Item>
        <Item ratio={1} baseSize="50%">
          { box(self, 'e-Receipt status') }
        </Item>
      </Box>
    </React.Fragment>
  );
}

function aChart(self, title) {
    const data = genDaily();
    return (
      <React.Fragment>
        <Chart
          id="zoomedChart"
          title={ title || '' }
          palette="Harmony Light"
          dataSource={data}
          style={{ flexGrow: 1 }}
        >
          <Series argumentField="d" valueField="success" name="Success" color="green" />
          <Series argumentField="d" valueField="error"   name="Error"   color="red" />
          <Series argumentField="d" valueField="failed"  name="Failed"  color="orange" />
          <ArgumentAxis visualRange={self.state.visualRange} />
          <Legend visible={true} />
          <CommonSeriesSettings>
            <Point size={7} />
          </CommonSeriesSettings>
          <Export enabled={true} printingEnabled={false} />
        </Chart>
      </React.Fragment>
    );
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        visualRange: { startValue: INITIAL_RANGE.start, endValue: INITIAL_RANGE.end }
    };
    this.updateVisualRange = this.updateVisualRange.bind(this);
  }
  updateVisualRange(e) {
    this.setState({ visualRange: e.value });
  }
  render() {
    const HEIGHT = "100%";
    return (
      <React.Fragment>
        <RangeSelector
          title="Period"
          dataSource={DATA}
          defaultValue={[INITIAL_RANGE.start, INITIAL_RANGE.end]}
          onValueChanged={this.updateVisualRange}
        >
          <Size height="175" />
          <Margin left={10} />
          <ChartOptions palette="Harmony Light">
            <Behavior callValueChanged="onMoving" />
            <Legend visible={true} />
            <Series argumentField="d" valueField="success" name="Success" color="green" />
            <Series argumentField="d" valueField="error"   name="Error"   color="red" />
            <Series argumentField="d" valueField="failed"  name="Failed"  color="orange" />
            </ChartOptions>
            <MinorTick visible={true} />
          <Scale
             minorTickCount={1}
             startValue={RANGE.start}
             endValue={RANGE.end}
             minorTickInterval="day"
             tickInterval="week"
             minRange="day"
             aggregateByCategory={true}
          >
          </Scale>
          <SliderMarker format="monthAndDay" />
        </RangeSelector>
        <div style={{margin: "0.5em", height: '50em'}}>
            <Box direction="col" height="100%">
            <Item ratio={1}>{ rowA(this) }</Item>
            <Item ratio={1}>{ rowB(this) }</Item>
          </Box>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
